/*layout full*/
body.theme-variation-landingpage {
  background: var(--theme-page-main-bg);
  
  nav#theme-page-navigation {
    background: linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, rgba(255,255,255,0) 100%);
    position: fixed;
    top:0;
    left:0;
    width:100%;
  }

  .theme-background-container {
    overflow: hidden;
  }
}
